body{
    font-family: 'Lato', sans-serif !important;
}
button{
    padding:10px;
    margin:5px;
    font-weight: 700;
}
.post,.post2{
    max-width: 502px !important;
    text-transform: capitalize !important;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: 0 0 10px 1px #ccc; 
    box-sizing: border-box;
    padding:40px !important;
    width:25%;
    border-top:0.01px solid transparent;
    border-bottom: 0.01px solid gray;
    transition: 1s all;
}

.post2{
    max-width: 100% !important;
    width:270px !important;
}
.post:hover{
    padding:20px !important;
    box-shadow: none;
    border-bottom:0.01px solid transparent;
}
.post2:hover{
    border-top:0.01px solid gray;
    box-shadow: none;
}
.post>p>img{
    filter: drop-shadow(1px 1px 6px #ccc);
}
.hr1{
    z-index: 5;
    margin:0;
    background-color: #ccc;
    border-radius: 50%;
    height: 10px;
    top:0;
    left:0;
    width: 12px;
    /* transition: 0.1  s all; */
}
/* .hr2{
    z-index: 10000;
    top:0;
    margin:0;
    width: 1px;
    height: 50px;
    background-color: red;
} */

.header{
    z-index: 2;
    position: sticky;
    width:100%;
    top:0;
    background-color: white;
}
.heading{
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.cards{
    max-width: 300px !important;
}

.headerLink{
    text-decoration: none !important;
    margin-left:1px;
    transition: 0.4s all;
    letter-spacing: 2px;
    font-weight: normal;
    border-bottom: 1px solid white;
    padding:5px 15px 5px 15px;
}
.headerLink:hover{  
    font-weight: 900;
}
.isActive{
    font-weight: 900;
    border-bottom:1px solid blueviolet;
}
.posts_container{
    position: sticky;
    top:10px;
    background:white;
    z-index: 1;
}
.username-desc{
    right:0;
    bottom: 0;
}
/* .display_none{
    display: none !important;
} */
.modal{
    text-transform: capitalize !important;
}
@media screen and (max-width:991px){
    .post{
        width:100% !important;
        max-width: 95% !important;
    }
}